/* header block */
.header {
  height: 350px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;

  -webkit-box-shadow: inset 0px -40px 100px 0 var(--body-bg);

          box-shadow: inset 0px -40px 100px 0 var(--body-bg);

  background: url('main-background.png') no-repeat 50% bottom;
  background-size: cover;
}

  /* pretitle */
  .header__pretitle{
    text-transform: uppercase;
    color: var(--color-gray);
  }
  /* title */
  .header__title{
    margin: 10px 0;

    color: var(--color-white);
  }
  /* subtitle */
  .header__subtitle{
    color: var(--color-white);
  }


