/* container */
@media(max-width: 1219px){
  .container{
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media(min-width: 1220px){
  .container{
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
}
.container{
  padding-top: 20px;
  padding-bottom: 20px;
}


